.header {
  display: flex;
  justify-content: space-between;
  max-width: 880px;
  min-height: 74px;
  margin: 0 auto;
  padding: 45px 0 0 0;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
}

@media screen and (max-width: 768px) {
  .header {
    max-width: 320px;
    min-height: 56px;
    padding: 28px 0 0 0;
  }
}